
          @import "@/styles/global.sass"
        





































































































































main
  max-width: 1300px
  width: 100%
  position: relative
.top-bar
  display: flex
  justify-content: space-between
  align-items: center
  position: relative

  .info
    position: relative
    display: flex
    align-items: center
    min-width: 0
    .languageName
      font-size: 2rem
      margin-right: 1em
    .codeInfo
      display: flex
      justify-content: space-between
      flex-direction: column
      overflow: hidden
      p
        margin-bottom: $grid-gap

  .buttons
    flex-shrink: 0
    button
      text-align: center
      width: 150px
      height: 47px
      background: $grid-color
      margin-left: max(10px, calc(20vw - 210px))
      cursor: pointer

.button
  flex-grow: 1
  max-width: 250px

@keyframes opacity-enter
  from
    opacity: 0
  to
    opacity: 1
@keyframes opacity-leave
  from
    opacity: 1
  to
    opacity: 0
.countdown
  position: fixed
  top: 0
  bottom: 0
  left: 0
  right: 0
  background-color: rgba($grid-color, .5)
  display: flex
  justify-content: space-around
  align-items: center
  pointer-events: none
  z-index: 10
  font-size: 4rem
  animation: opacity-enter 0.5s ease-out forwards
  animation-fill-mode: both
  // transition: opacity .7s ease-out

  &.started
    animation: opacity-leave 0.5s ease-out forwards


